<template>
    <div class="content">
            <h1 class="title">生活指数</h1>
            <div class="searchCity">
                <span>请输入城市名：</span>
                <input type="text" value="" @keydown.enter="inputCity" v-model="city">
                <button class="sweather" @click="inputCity">查看生活指数</button>
            </div>
            <h3>{{nowCity}}</h3>

            <h3>{{comf}}</h3>
            <h3>{{comf_txt}}</h3>

            <h3>{{drsg}}</h3>
            <h3>{{drsg_txt}}</h3>

            <h3>{{flu}}</h3>
            <h3>{{flu_txt}}</h3>

            <h3>{{sport}}</h3>
            <h3>{{sport_txt}}</h3>

            <h3>{{air}}</h3>
            <h3>{{air_txt}}</h3>

            <h3>{{cw}}</h3>
            <h3>{{cw_txt}}</h3>
        </div>
</template>

<style scoped>
.content h3{
    margin: 10px auto;
}

.searchCity input{
    height: 30px;
    widows: 170px;
}

.title{
    margin: 20px auto;
}

.sweather{
    margin-left: 10px;
    color: #fff;
    background: #333;
    width: 90px;
    height: 30px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.2s linear;
}

.sweather:hover{
    background: #fff;
    color: #333;
}
</style>

<script>
import axios from 'axios'

export default {
    data(){
        return {
            city:'',
            nowCity:'',
            comf:'',
            comf_txt:'',
            drsg:'',
            drsg_txt:'',
            flu:'',
            flu_txt:'',
            sport:'',
            sport_txt:'',
            air:'',
            air_txt:'',
            cw:'',
            cw_txt:''
        }
    },

    methods:{
        inputCity:async function(){
            let key = '3030f7e91ad6464ebeb9941f593a6bf9';
            let httpUrl = `https://free-api.qweather.com/s6/weather/lifestyle?location=${this.city}&key=${key}`;
            let res = await axios.get(httpUrl);
            // console.log(res);
            let data = res.data;
            // console.log(data);
            this.nowCity = this.city;

            this.comf = '舒适度指数：' + data.HeWeather6[0].lifestyle[0].brf;
            this.comf_txt = '舒适度指数详述：' + data.HeWeather6[0].lifestyle[0].txt;

            this.drsg = '穿衣指数：' + data.HeWeather6[0].lifestyle[1].brf;
            this.drsg_txt = '穿衣指数详述：' + data.HeWeather6[0].lifestyle[1].txt;

            this.flu = '感冒指数：' + data.HeWeather6[0].lifestyle[2].brf;
            this.flu_txt = '感冒指数详述：' + data.HeWeather6[0].lifestyle[2].txt;

            this.sport = '运动指数：' + data.HeWeather6[0].lifestyle[3].brf;
            this.sport_txt = '运动指数详述：' + data.HeWeather6[0].lifestyle[3].txt;

            this.air = '空气污染指数：' + data.HeWeather6[0].lifestyle[7].brf;
            this.air_txt = '空气污染指数详述：' + data.HeWeather6[0].lifestyle[7].txt;

            this.cw = '洗车指数：' + data.HeWeather6[0].lifestyle[6].brf;
            this.cw_txt = '洗车指数详述：' + data.HeWeather6[0].lifestyle[6].txt;

            this.city = '';
        },
    },
}
</script>