<template>
    <div class="weather">
        <now-com v-show='showNow'></now-com>
        <forecast-com v-show='showForecast'></forecast-com>
        <lifestyle-com v-show="showLifestyle"></lifestyle-com>
        <div class="aleft">
            <ul>
                <li v-for="(item,index) in itemList" :key="index">
                    <span @click="chooseItem(index)">{{item}}</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<style scoped>
.weather{
    margin-top: 70px;
}

.aleft{
    position: fixed;
    left: 0;
    top: 70px;
    width: 100px;
    height: 100%;
    background: #333;
    color: #757574;
    text-align: left;
}

.aleft li{
    margin: 30px auto;
    margin-left: 20px;
}

.aleft span{
    color: #757574;
    text-decoration: none;
    cursor: pointer;
}

.aleft span:hover{
    color: #fff;
}

</style>

<script>
import nowCom from '../components/nowWeather.vue'
import forecastCom from '../components/forecastWeather.vue'
import lifestyleCom from '../components/lifestyle.vue'

export default {
    data(){
        return {
            itemList:['实况天气','天气预报','生活指数'],
            showNow:true,
            showForecast:false,
            showLifestyle:false
        }
    },
    methods:{
        chooseItem:function(index){
            if(index == 0){
                this.showForecast = false;
                this.showLifestyle = false;
                this.showNow = true;
            }else if(index == 1){
                this.showNow = false;
                this.showLifestyle = false;
                this.showForecast = true;
            }else if(index == 2){
                this.showNow = false;
                this.showForecast = false;
                this.showLifestyle = true;
            }
        }
    },
    name:'weather',
    components:{
        nowCom,
        forecastCom,
        lifestyleCom
    },
}
</script>