<template>
    <div class="content">
            <h1 class="title">实况天气</h1>
            <div class="searchCity">
                <span>请输入城市名：</span>
                <input type="text" value="" @keydown.enter="inputCity" v-model="city">
                <button class="sweather" @click="inputCity">查看实况天气</button>
            </div>
            <h3>{{nowCity}}</h3>
            <h3 class="tmp">{{tmp}}</h3>
            <h3>{{cond}}</h3>
            <h3>{{windDir}}</h3>
            <h3>{{windSc}}</h3>
            <h3>{{vis}}</h3>
            <h3>{{pcpn}}</h3>
            <h3>{{hum}}</h3>
        </div>
</template>

<style scoped>
.content h3{
    margin: 10px auto;
}

.searchCity input{
    height: 30px;
    widows: 170px;
}

.title{
    margin: 20px auto;
}

.sweather{
    margin-left: 10px;
    color: #fff;
    background: #333;
    width: 90px;
    height: 30px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.2s linear;
}

.sweather:hover{
    background: #fff;
    color: #333;
}

.tmp{
    font-size: 60px;
}
</style>

<script>
import axios from 'axios'

export default {
    data(){
        return {
            city:'',
            nowCity:'',
            tmp:'',
            cond:'',
            windDir:'',
            windSc:'',
            vis:'',
            pcpn:'',
            hum:''
        }
    },

    methods:{
        inputCity:async function(){
            let key = '3030f7e91ad6464ebeb9941f593a6bf9';
            let httpUrl = `https://free-api.qweather.com/s6/weather/now?location=${this.city}&key=${key}`;
            let res = await axios.get(httpUrl);
            // console.log(res);
            let data = res.data;
            // console.log(data);
            this.nowCity = this.city;
            this.tmp = data.HeWeather6[0].now.tmp + '℃';
            this.cond = '当前天气状况：' + data.HeWeather6[0].now.cond_txt;
            this.windDir = '当前风向：' + data.HeWeather6[0].now.wind_dir;
            this.windSc = '当前风力：' + data.HeWeather6[0].now.wind_sc;
            this.vis = '当前能见度：' + data.HeWeather6[0].now.vis + '公里';
            this.pcpn = '当前降水量：' + data.HeWeather6[0].now.pcpn +'毫米';
            this.hum = '当前相对湿度：' + data.HeWeather6[0].now.hum +'%RH';
            this.city = '';
        },
    },
}
</script>