<template>
    <div class="content">
            <h1 class="title">天气预报</h1>
            <div class="searchCity">
                <span>请输入城市名：</span>
                <input type="text" value="" @keydown.enter="inputCity" v-model="city">
                <button class="sweather" @click="inputCity">查看天气预报</button>
            </div>
            <h3>{{nowCity}}</h3>

            <span class="tmp">{{tmp_min1}}</span>
            <span class="tmp">{{tmp_max1}}</span>
            <h3>{{date1}}</h3>
            <h3>{{sr1}}</h3>
            <h3>{{ss1}}</h3>
            <h3>{{cond_d1}}</h3>
            <h3>{{cond_n1}}</h3>

            <span class="tmp">{{tmp_min2}}</span>
            <span class="tmp">{{tmp_max2}}</span>
            <h3>{{date2}}</h3>
            <h3>{{sr2}}</h3>
            <h3>{{ss2}}</h3>
            <h3>{{cond_d2}}</h3>
            <h3>{{cond_n2}}</h3>

            <span class="tmp">{{tmp_min3}}</span>
            <span class="tmp">{{tmp_max3}}</span>
            <h3>{{date3}}</h3>
            <h3>{{sr3}}</h3>
            <h3>{{ss3}}</h3>
            <h3>{{cond_d3}}</h3>
            <h3>{{cond_n3}}</h3>
        </div>
</template>

<style scoped>
.content h3{
    margin: 10px auto;
}

.searchCity input{
    height: 30px;
    widows: 170px;
}

.title{
    margin: 20px auto;
}

.sweather{
    margin-left: 10px;
    color: #fff;
    background: #333;
    width: 90px;
    height: 30px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.2s linear;
}

.sweather:hover{
    background: #fff;
    color: #333;
}

.tmp{
    font-size: 60px;
    font-weight: bold;
}
</style>

<script>
import axios from 'axios'

export default {
    data(){
        return {
            city:'',
            nowCity:'',

            date1:'',
            date2:'',
            date3:'',

            tmp_max1:'',
            tmp_min1:'',
            tmp_max2:'',
            tmp_min2:'',
            tmp_max3:'',
            tmp_min3:'',

            cond_d1:'',
            cond_n1:'',
            cond_d2:'',
            cond_n2:'',
            cond_d3:'',
            cond_n3:'',

            sr1:'',
            ss1:'',
            sr2:'',
            ss2:'',
            sr3:'',
            ss3:'',
        }
    },

    methods:{
        inputCity:async function(){
            let key = '3030f7e91ad6464ebeb9941f593a6bf9';
            let httpUrl = `https://free-api.qweather.com/s6/weather/forecast?location=${this.city}&key=${key}`;
            let res = await axios.get(httpUrl);
            // console.log(res);
            let data = res.data;
            // console.log(data);
            this.nowCity = this.city;

            this.date1 = data.HeWeather6[0].daily_forecast[0].date;
            this.date2 = data.HeWeather6[0].daily_forecast[1].date;
            this.date3 = data.HeWeather6[0].daily_forecast[2].date;

            this.tmp_max1 = data.HeWeather6[0].daily_forecast[0].tmp_max + '℃';
            this.tmp_min1 = data.HeWeather6[0].daily_forecast[0].tmp_min + '℃ ~ ';
            this.tmp_max2 = data.HeWeather6[0].daily_forecast[1].tmp_max + '℃';
            this.tmp_min2 = data.HeWeather6[0].daily_forecast[1].tmp_min + '℃ ~ ';
            this.tmp_max3 = data.HeWeather6[0].daily_forecast[2].tmp_max + '℃';
            this.tmp_min3 = data.HeWeather6[0].daily_forecast[2].tmp_min + '℃ ~ ';

            this.cond_d1 = '白天天气：' + data.HeWeather6[0].daily_forecast[0].cond_txt_d;
            this.cond_n1 = '夜晚天气：' + data.HeWeather6[0].daily_forecast[0].cond_txt_n;
            this.cond_d2 = '白天天气：' + data.HeWeather6[0].daily_forecast[1].cond_txt_d;
            this.cond_n2 = '夜晚天气：' + data.HeWeather6[0].daily_forecast[1].cond_txt_n;
            this.cond_d3 = '白天天气：' + data.HeWeather6[0].daily_forecast[2].cond_txt_d;
            this.cond_n3 = '夜晚天气：' + data.HeWeather6[0].daily_forecast[2].cond_txt_n;

            this.sr1 = '日升时间：' + data.HeWeather6[0].daily_forecast[0].sr;
            this.ss1 = '日落时间：' + data.HeWeather6[0].daily_forecast[0].ss;
            this.sr2 = '日升时间：' + data.HeWeather6[0].daily_forecast[1].sr;
            this.ss2 = '日落时间：' + data.HeWeather6[0].daily_forecast[1].ss;
            this.sr3 = '日升时间：' + data.HeWeather6[0].daily_forecast[2].sr;
            this.ss3 = '日落时间：' + data.HeWeather6[0].daily_forecast[2].ss;

            this.city = '';
        },
    },
}
</script>